import { useQuery } from "@tanstack/react-query"
import { quotaMaxAllowedValues } from "./quotaData"

// -----------------
// Query Keys
// -----------------

const quotaQueryKeys = {
  all: () => ["quotaAllowedValues"],
  quota: (entitlement: string) => ["quotaAllowedValues", entitlement] as const,
}

// -----------------
// Network Requests
// -----------------

const getAllQuotaMaxAllowedValues = async () => {
  return quotaMaxAllowedValues
}

// -----------------
// Query Hooks
// -----------------

const useQuotaDetails = () => {
  return useQuery({
    queryKey: quotaQueryKeys.all(),
    queryFn: getAllQuotaMaxAllowedValues,
  })
}

export { useQuotaDetails }
